<template>
    <div class="bindPassword">
        <el-form class="password-form">
            <div>
                <div class="phone-box">
                    <el-input v-model="oldPassWord" size="medium" show-password :placeholder="$L('请输入6-20位字母和数字组合新密码')"></el-input>
                    <el-input v-model="newPassWord" size="medium" show-password :placeholder="$L('请再次输入新密码')"></el-input>
                </div>
                <div class="phone-box">
                    <el-input v-model="encryptionPhone" :placeholder="$L('输入手机号')" disabled></el-input>
                    <el-input v-model="mobileCode" :placeholder="$L('输入手机验证码')">
                        <el-button @click="getPhoneMessage" v-show="show" class="getMess" slot="append">{{$L(' 获取验证码')}}</el-button>
                        <el-button v-show="!show" class="getMess" slot="append">{{ count }}</el-button>
                    </el-input>

                    <el-button type="primary" @click="bindFunc(0)">{{ $L('确认修改') }}</el-button>
                </div>
            </div>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "bindOldPassword",
        inject: ["reload"],
        props: {
            data: {
                type:Object,
                default: {}
            }
        },
        data(){
            return{
                isBind:1,
                phoneNumber:'',
                oldPassWord:'',
                newPassWord:'',
                mobileCode:'',
                count: '',
                show: true,
                encryptionPhone:'',
            }
        },
        created(){
            this.getData();
        },
        methods:{
            getData() {
                const _self = this;

                _self.loading = true;
                _self.$getApi('forgetPassword',{}, function (reData) {
                    _self.encryptionPhone = reData.mobileNum;
                    _self.phoneNumber = reData.trueMobileNum;
                    _self.loading = false;

                }, function (error) {
                    if(error && error.flag == -2){
                        _self.$alert(_self.$L('尚未设置登录密码！无法解绑！'), _self.$L('解绑QQ失败!'), {
                            confirmButtonText: _self.$L('确定'),
                            type: 'error',
                            showClose: false
                        }).then(() => {
                            _self.loading = false;
                            _self.getData();
                        });
                    }else{
                        _self.$confirm(_self.$L('解绑QQ失败!'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.untiedQQ();
                        }).catch(action => {
                            _self.loading = false;
                            _self.getData();
                        });
                    }
                });

            },
            bindFunc(isset){

                if (this.mobileCode.trim() === '') {
                    this.$message.error(this.$L("请输入验证码"));
                    return;
                }
                if (this.oldPassWord.trim() === '' ||　this.newPassWord.trim() === '') {
                    this.$message.error(this.$L("请输入密码"));
                    return;
                } else if (this.oldPassWord.trim() !== this.newPassWord.trim() && isset == 0) {
                    this.$message.error(this.$L("密码不一致"));
                    return;
                } else {
                    if (this.oldPassWord.trim() !== '' ||　this.newPassWord.trim() !== '') {
                        let reg=/^[a-zA-Z0-9]{6,20}$/;
                        if(!reg.test(this.newPassWord.trim())){
                            this.$message.error(this.$L("抱歉, 请设置6-20位包含字母数字组合的密码"));
                            return;
                        } else if (!reg.test(this.oldPassWord.trim())) {
                            this.$message.error(this.$L("抱歉, 请设置6-20位包含字母数字组合的密码"));
                            return;
                        }
                    }
                }

                const _self = this;
                _self.loading = true;
                let arr = {
                    oldPassWord: _self.oldPassWord,
                    newPassWord:_self.newPassWord,
                    isSetPassword:1,
                    isPhone:1,
                    type:1,
                    mobile:_self.phoneNumber,
                    code_type:'sms',
                    code:_self.mobileCode,
                }

                _self.$api('setPassWord', arr, function (reData) {
                    if (reData.flag === 1) {

                        _self.$message({
                            message: _self.$L("密码设置成功！"),
                            type:'success'
                        });
                        _self.$emit("closeDialog");

                        _self.loading = false;
                        _self.reload();
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L(reData.msg), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.reload();
                            _self.$emit("closeDialog");
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.reload();
                        _self.$emit("closeDialog");
                    }).catch(action => {
                        _self.$gotoWebHome();
                    });
                });

            },
            getPhoneMessage(){

                const _self = this;
                _self.loading = true;
                let arr = {
                    send_mobile: 1

                };
                _self.$api('sendCodeChange', arr, function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;

                        const TIME_COUNT = _self.tableData.limitTime;

                        if (!_self.timer) {
                            _self.count = TIME_COUNT;
                            _self.show = false;
                            _self.timer = setInterval(() => {
                                if (_self.count > 0 && _self.count <= TIME_COUNT) {
                                    _self.count--;
                                } else {
                                    _self.show = true;
                                    clearInterval(_self.timer);
                                    _self.timer = null;
                                }
                            }, 1000)
                        }

                        _self.$message({
                            message: _self.$L('手机验证码发送成功咯！'),
                            type: 'success'
                        })

                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                            _self.$emit("closeDialog");
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L(reData.msg), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.reload();
                            _self.$emit("closeDialog");
                        }).catch(action => {

                            if (action == 'cancel') {
                                _self.$gotoWebHome();
                            } else {
                                _self.reload();
                            }
                            _self.$emit("closeDialog");
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.reload();
                        _self.$emit("closeDialog");
                    }).catch(action => {
                        _self.$gotoWebHome();
                        _self.$emit("closeDialog");
                    });
                });

            },
        }
    }
</script>

<style lang="scss" scoped>
    .bindPassword{
        position: relative;
        height: 400px;
        .password-form{
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            top: 50px;
            text-align: center;
            width: 100%;
            .title{
                font-size: 16px;
            }
            .remind{
                font-size: 12px;
                color: #999999;
                margin-top: 10px;
            }
            .phone-box{
                .el-input{
                    width: 300px;
                    margin-bottom: 30px;
                }
                .el-button{
                    font-size: 14px;
                    display: block;
                    margin: 0 auto;
                    width: 300px;
                }
                .getMess{
                    line-height: 34px;
                    width: 80px;
                    padding: 0;
                    text-align: center;
                }
                .tips{
                    width: 300px;
                    margin: 0 auto 20px;
                    text-align: left;
                    cursor: pointer;
                }
            }

        }
    }
</style>