<template>
    <div class="bindPassword">
        <el-form class="password-form">
            <div v-if="data.isset === 0">
                <div class="phone-box">
                    <el-input v-model="oldPassWord" size="medium" show-password :placeholder="$L('请输入6-20位字母和数字组合新密码')"></el-input>
                    <el-input v-model="newPassWord" size="medium" show-password :placeholder="$L('请再次输入新密码')"></el-input>
                    <el-button type="primary" @click="bindFunc(0)">{{ $L('确认') }}</el-button>
                </div>
            </div>
            <div v-else-if="data.isset === 1">
                <div class="phone-box">
                    <el-input size="medium" show-password v-model="oldPassWord" :placeholder="$L('请输入原密码')"></el-input>
                    <el-input size="medium" show-password v-model="newPassWord" :placeholder="$L('请输入6-20位字母和数字组合新密码')"></el-input>
                    <p class="tips mainFontColor" @click="forgotOldFunc" v-if="data.phoneSet == 1"> {{ $L('忘记旧密码') }}</p>
                    <el-button type="primary" @click="bindFunc(2)">{{ $L('确认修改') }}</el-button>
                </div>
            </div>

        </el-form>
    </div>
</template>

<script>
    import bindOldPassword from './bindOldPassword.vue';
    export default {
        name: "bindPassword",
        inject: ["reload"],
        props: {
            data: {
                type:Object,
                default: {}
            }
        },
        components:{
            bindOldPassword
        },
        data(){
            return{
                isBind:1,
                oldPassWord:'',
                newPassWord:'',
                isSetPassword:0,
            }
        },
        methods:{
            bindFunc(isset){

                switch (isset) {
                    case 0:
                        this.isSetPassword = 1;
                        break;
                    case 1:

                        break;
                    case 2:
                        break;
                }

                if (this.oldPassWord.trim() === '' ||　this.newPassWord.trim() === '') {
                    this.$message.error(this.$L("请输入密码"));
                    return;
                } else if (this.oldPassWord.trim() !== this.newPassWord.trim() && isset == 0) {
                    this.$message.error(this.$L("密码不一致"));
                    return;
                } else {
                    if (this.oldPassWord.trim() !== '' ||　this.newPassWord.trim() !== '') {
                        let reg=/^[a-zA-Z0-9]{6,20}$/;
                        if(!reg.test(this.newPassWord.trim())){
                            this.$message.error(this.$L("抱歉, 请设置6-20位包含字母数字组合的密码"));
                            return;
                        } else if (!reg.test(this.oldPassWord.trim())) {
                            this.$message.error(this.$L("抱歉, 请设置6-20位包含字母数字组合的密码"));
                            return;
                        }
                    }
                }

                const _self = this;
                _self.loading = true;
                let arr = {
                    oldPassWord: _self.oldPassWord,
                    newPassWord:_self.newPassWord,
                    isSetPassword:_self.isSetPassword,
                }

                _self.$api('setPassWord', arr, function (reData) {
                    if (reData.flag === 1) {

                        _self.$message({
                            message: _self.$L("密码设置成功！"),
                            type:'success'
                        });
                        _self.$emit("closeDialog");

                        _self.loading = false;
                        _self.reload();
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L(reData.msg), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.bindFunc();
                        }).catch(action => {

                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.reload();
                        _self.$emit("closeDialog");
                    }).catch(action => {

                    });
                });

            },
            forgotOldFunc(){
                this.$store.commit('setDialogObj', {
                    name: this.$L('设置密码'),
                    view: bindOldPassword,
                    width: '500px',
                    data: {

                    }

                });
            }
        }
    }
</script>

<style lang="scss" scoped>
    .bindPassword{
        position: relative;
        height: 400px;
        .password-form{
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            top: 50px;
            text-align: center;
            width: 100%;
            .title{
                font-size: 16px;
            }
            .remind{
                font-size: 12px;
                color: #999999;
                margin-top: 10px;
            }
            .phone-box{
                margin-top: 30px;
                .el-input{
                    width: 300px;
                    margin-bottom: 30px;
                }
                .el-button{
                    font-size: 14px;
                    display: block;
                    margin: 0 auto;
                    width: 300px;
                }
                .getMess{
                    line-height: 34px;
                    width: 80px;
                    padding: 0;
                    text-align: center;
                }
                .tips{
                    width: 300px;
                    margin: 0 auto 20px;
                    text-align: left;
                    cursor: pointer;
                }
            }

        }
    }
</style>