<template>
    <div class="bind-qq" v-loading="loading">
        <iframe :src="url" v-if="!loading" class="window"></iframe>
    </div>
</template>

<script>
    export default {
        name: "bindQQ",
        inject: ["reload"],
        data(){
            return {
                loading:true,
                url:'',
            };
        },
        computed:{
            needOpen:{
                get(){
                    let ua = navigator.userAgent.toLowerCase();
                    if(ua.indexOf(' qq')>-1 && ua.indexOf('mqqbrowser') <0){
                        //qq内置浏览器
                        return true;
                    }
                    if(ua.indexOf('mqqbrowser')> -1 && ua.indexOf(" qq")<0){
                        //qq浏览器
                        return true;
                    }
                    if (ua.match(/MicroMessenger/i) == 'micromessenger') {
                        //微信浏览器
                        return true;
                    }
                    return false;
                }
            }
        },
        created(){
            this.getUrl();
        },
        methods:{
            getUrl(){
                const _self = this;
                _self.loading = true;
                _self.$api('bindQQ', {domain:_self.$getLocationUrl(),localUrl:window.location.href}, function (reData) {
                    if (reData.flag === 1) {
                        if(_self.needOpen){
                            window.top.location.href = reData.data.url;
                        }else{
                            _self.url = reData.data.url;
                            _self.loading = false;
                        }
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L(reData.msg), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.reload();
                            _self.$emit("closeDialog");
                        }).catch(action => {
                            if (action == 'cancel') {
                                _self.$gotoWebHome();
                            }
                            _self.$emit("closeDialog");
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.reload();
                        _self.$emit("closeDialog");
                    }).catch(action => {
                        _self.$gotoWebHome();
                        _self.$emit("closeDialog");
                    });
                });

            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../assets/css/common/lib.scss";
    .bind-qq{
        position: relative;
        width: 100%;
        height: 600px;
        .window{
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 100;
            /deep/ .mi-ui{
                overflow: hidden;
            }
        }
    }
</style>