<template>
    <div class="bind-we-chat" v-loading="loading">
        <div v-if="!loading">
            <div class="window" v-if="url">
                <span class="spanFont"> {{ isWx?$L('长按识别二维码'):$L('请使用微信扫描二维码') }} </span>
                <br>
                <img class="image" :src="url"/>
            </div>
            <div class="window" v-else><span class="spanFont">{{errorMsg}}</span></div>
        </div>
    </div>

</template>

<script>
    export default {
        name: "bindWeChat",
        data() {
            return {
                loading: true,
                url: null,
                errorMsg: this.$L('数据加载失败'),
            };
        },
        computed: {
            isWx: {
                get() {
                    let ua = navigator.userAgent.toLowerCase();
                    if (ua.match(/MicroMessenger/i) == 'micromessenger') {
                        //微信浏览器
                        return true;
                    }
                    return false;
                }
            }
        },
        created() {
            this.getUrl();
        },
        methods: {
            getUrl() {
                const _self = this;
                _self.loading = true;
                _self.$getApi('bindWeChat', {localUrl: window.location.href}, function (reData) {
                    _self.url = reData.url;
                    _self.loading = false;
                }, function (error) {
                    //网络请求错误
                    if (error && error.hasOwnProperty('msg')) {
                        _self.errorMsg = error.msg;
                    }
                    _self.loading = false;
                });

            }
        }

    }
</script>

<style lang="scss" scoped>
    @import "../../assets/css/common/lib.scss";

    .bind-we-chat {
        position: relative;
        width: 100%;
        height: 500px;

        .window {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 100;
            text-align: center;
            padding: 20px;

            .spanFont {
                font-size: 20px;
            }

            .image {
                display: block;
                margin: 0 auto;
                width: 300px;
                height: 300px;
                transform: translateY(35%);
            }
        }
    }
</style>